import { NextResponse } from "next/server";

import { getSlideAudioUrl } from "@/app/material/editor/servers/getSlideAudioUrl";

export async function POST(request: Request) {
  try {
    const body = await request.json();
    const { userUuid, slideNumber, voiceId } = body as {
      userUuid?: string;
      slideNumber?: number;
      voiceId?: string;
    };

    if (!userUuid || !voiceId || typeof slideNumber !== "number") {
      return NextResponse.json(
        { message: "Неверные параметры запроса" },
        { status: 400 },
      );
    }

    const url = await getSlideAudioUrl({
      userUuid,
      slideNumber,
      voiceId,
    });

    return NextResponse.json({ url });
  } catch (error) {
    console.error("[slide-audio-url] error", error);
    return NextResponse.json(
      { message: "Не удалось получить ссылку на аудио" },
      { status: 500 },
    );
  }
}

