"use server";

type GetSlideAudioUrlParams = {
  userUuid: string;
  slideNumber: number;
  voiceId: string;
};

const BASE_S3_URL = "https://s3.twcstorage.ru/da2ac12a-41cc63a3-3ad9-4eee-a33a-7776f7973de7";

export async function getSlideAudioUrl({
  userUuid,
  slideNumber,
  voiceId,
}: GetSlideAudioUrlParams): Promise<string> {
  if (!userUuid) {
    throw new Error("Не удалось определить пользователя");
  }
  if (!voiceId) {
    throw new Error("Не указан voice_id для слайда");
  }
  if (!Number.isFinite(slideNumber) || slideNumber <= 0) {
    throw new Error("Неверный номер слайда");
  }

  const safeUuid = encodeURIComponent(userUuid.trim());
  const safeVoiceId = encodeURIComponent(voiceId.trim());

  return `${BASE_S3_URL}/temp/${safeUuid}/TTS/${slideNumber}_${safeVoiceId}`;
}

